body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html, body {
  min-width: 0 !important;
  background-color: #061B84;
}

#zmmtg-root {
  display: none;
}

.q-a-welcome {
  height: 300px !important;
}

.sharee-sharing-indicator__tip {
  display: none !important;
}

.sharee-container__canvas-outline {
  border: none !important;
}

.q-a-container__tab-content-wrapper {
  max-height: 300px !important;;
}

.notification-message-wrap {
  display: none !important;
}

.footer {
  background-image: linear-gradient(to bottom, #183981, #193881) !important;
}

.zmu-btn--danger {
  background: #0e71eb !important;
}

.video-avatar__avatar-footer {
  display: none !important;
}

.sharee-sharing-indicator {
  display: none !important;
}